.App {

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  text-align: center;
  color: black;
}

table{
    width: 100%;
    text-align: center;
    font-size: 15px ;
    color: black;
    border: 1px solid white;

    width: 100%;
}

th, td {
    border: 1px solid white;
    padding: 10px;
    text-align: center;
}

th {
    border-bottom: 5px solid white;
    color: white;
    font-weight: bold;
    background-color: #5082bd;
}

td input {
    width: 100%;
    border: none;
    text-align: center;
    background-color: inherit;
}

tr:nth-child(even) {
    background-color: #e9edf4;
}

tr:nth-child(odd) {
    background-color: #d0d8e8;
}


th:nth-child(-n+2), th:nth-child(4), th:nth-child(9), th:nth-child(10),th:nth-child(11),
td:nth-child(-n+2), td:nth-child(4), td:nth-child(9), td:nth-child(10),td:nth-child(11)
{
    width: 50px;
}

th:nth-child(n+5):nth-child(-n+8), td:nth-child(n+5):nth-child(-n+8) {
    width: 130px;
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


